import React from 'react'
import './Main.scss'
import Paper from '@mui/material/Paper';
import Carousel from '../Carousel/Carousel';
import TextandImage from '../TextandImage/TextandImage';



export default function Main() {


  const style = {
    paper: {
      position: 'relative',
      width: '80%'
    }
  }

  return (
    <div>
        <main className='main'>
                <Paper elevation={3} style={style.paper}>
                <div className="main__header">
                    Strona w ktrej wykorzystana zostaa metoda CRUD. W apikacji moemy dodawa, edytowa i usuwa produkty. Moemy rwnie sortowa poszczeglne prezenty po rnych wariantach. 
                </div>
                </Paper>  
        </main>
        <Carousel />
        <TextandImage />
    </div>
  )
}
